import React from 'react'
import Layout from '../../containers/Layout'
import H2 from '../../components/Headings/H2'
import H4 from '../../components/Headings/H4'
import Lead from '../../components/Text/Lead'
import P from '../../components/Text/P'
import CodeCard from '../../components/CodeCard'
import ExampleSection from '../../components/ExampleSection'

const badgeSizesCode = `
<span class="rounded-full h-2 w-2 inline-block bg-red-500"></span>
<span class="rounded-full h-4 w-4 inline-flex items-center justify-center text-xs text-red-100 bg-red-500">9</span>
`

function Badge() {
  return (
    <Layout>
      <H2>Badge</H2>
      <Lead>Badge component using Tailwind CSS to speed up your project.</Lead>
      <hr className="my-8" />

      <ExampleSection id="badge-sizes">
        <H4>Badge sizes</H4>
        <P>Different sized badges.</P>
        <CodeCard lang="html" code={badgeSizesCode} />
      </ExampleSection>
    </Layout>
  )
}

export default Badge
